// Padding Helpers

%vpadding {
	padding-top: $cx-section-spacing;
	padding-bottom: $cx-section-spacing;
	@include respond-to(mobile) {
		padding-top: $cx-section-spacing-mob;
		padding-bottom: $cx-section-spacing-mob;
	}
}

%pb-full {
	padding-bottom: $cx-section-spacing;
	@include respond-to(mobile) {
		padding-bottom: $cx-section-spacing-mob;
	}
}

%pt-full {
	padding-top: $cx-section-spacing;
	@include respond-to(mobile) {
		padding-top: $cx-section-spacing-mob;
	}
}

%pb-half {
	padding-bottom: $cx-col-spacing;
	@include respond-to(mobile) {
		padding-bottom: $cx-col-spacing-mob;
	}
}

%pt-half {
	padding-top: $cx-col-spacing;
	@include respond-to(mobile) {
		padding-top: $cx-col-spacing-mob;
	}
}

%padding-0 {
	padding: 0;
}

%pt0 {
	padding-top: 0;
}

%pb0 {
	padding-bottom: 0;
}


