	}
}

%margin-0 {
	margin: 0;
}

%mt0 {
	margin-top: 0;
}

%mb0 {
	margin-bottom: 0;
}


// Hide an element

%visuallyhidden {
	margin: -1px;
	padding: 0;
	width: 1px;
	height: 1px;
	overflow: hidden;
	clip: rect(0 0 0 0);
	clip: rect(0, 0, 0, 0);
	position: absolute;
}


// Common Helpers

%clearfix {
	&::before,
    &::after {
        content: "";
        display: table;
    }

    &::after {
        clear: both;
    }
    zoom: 1;
}

%default-heading {
	line-height: 1;
	text-transform: uppercase;
    margin-top: 0;
    margin-bottom: $cx-heading-margin-bottom;
}

%cx-transition {
	transition: $cx-transition;
}


// Vertically and Horizantally Centering

%display-flex {
	@include flex;
}

%vhcenter {
	@extend %display-flex ;
	align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
}


// Only vertical centering

%vcenter {
	@extend %display-flex ;
	align-items: center;	
}

%flex_end {
	@extend %display-flex ;
	justify-content:  flex-end ;
}


// Border Helpers

%cx-top-border {
	@include cx-border(1px, 0, 0, 0);
}

%cx-right-border {
	@include cx-border(0, 1px, 0, 0);
}

%cx-bottom-border {
	@include cx-border(0, 0, 1px, 0);
}

%cx-left-border {
	@include cx-border(0, 0, 0, 1px);
}

%cx-right-bottom-border {
